import { Controller } from '@ali/4ever-cangjie';
import {
  LIST_REMOVE_LIST,
  LIST_SET_CHECK_STATUS,
  LIST_TOGGLE_LIST,
  LIST_TRANSFORM,
} from '../actions';
import {
  toggleWrapInList,
  transform,
  removeListNode,
  toggleCheckStatus,
} from '../commands';

export default function onAction(action, controller: Controller, next) {
  const { type, payload } = action;
  if (type === LIST_TOGGLE_LIST) {
    const { listType, listStyleType } = payload;
    return toggleWrapInList(controller, listType, listStyleType);
  }
  if (type === LIST_TRANSFORM) {
    const { listType, listStyleType } = payload;
    return transform(controller, listType, listStyleType);
    // return controller.command(transform, listType, listStyleType);
  }
  if (type === LIST_REMOVE_LIST) {
    return removeListNode(controller);
  }
  if (type === LIST_SET_CHECK_STATUS) {
    const { node, checked } = payload;
    return toggleCheckStatus(controller, node.key, checked);
  }
  return next();
}
